import 'bulma/css/bulma.css';
import '@fortawesome/fontawesome-free/css/all.css';
import './App.scss';
import {
  Routes,
  Route,
  NavLink,
  Navigate,
} from 'react-router-dom';
import classNames from 'classnames';

import { HomePage } from './components/pages/HomePage';
import { TabsPage } from './components/pages/TabsPage';
import { WrongPage } from './components/pages/WrongPage';

const getLinkClass = ({ isActive }: { isActive: boolean }) => classNames(
  'navbar-item',
  {
    'is-active': isActive,
  },
);

const getLinkStyle = (
  { isActive }: { isActive: boolean },
) => (
  { color: isActive ? 'red' : '' }
);

export const App = () => (
  <>
    <nav
      className="navbar is-light is-fixed-top is-mobile has-shadow "
      data-cy="Nav"
    >
      <div className="container">
        <div className="navbar-brand">
          <NavLink to="/" className={getLinkClass} style={getLinkStyle}>
            Home
          </NavLink>
          <NavLink to="/tabs" className={getLinkClass} style={getLinkStyle}>
            Tabs
          </NavLink>
        </div>
      </div>
    </nav>

    <div className="section">
      <div className="container">
        <Routes>
          <Route path="/" element={<HomePage />} />
          <Route path="home" element={<Navigate to="/" replace />} />

          <Route path="tabs">
            <Route index element={<TabsPage />} />
            <Route path=":tabId" element={<TabsPage />} />
          </Route>

          <Route path="*" element={<WrongPage />} />
        </Routes>
      </div>
    </div>
  </>
);
